<template>
  <div id="relation">
    <!--  -->
    <div v-if="this.$store.state.Language=='Chinese'">
      <div class="zsb_relation">
        <img src="../../static/images/zsb_lianxi.png" alt="">
      </div>
      <div class="zsb_guanyu_lianxi">
        <div class="w">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/relation' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
          <div class="guanyu_lianxi">
            <div class="guanyu_left left">
              <div class="active">{{text}}</div>
              <ul>
                <li @click="textCut(1)"></li>
                <li @click="textCut(2)"></li>
              </ul>
            </div>
            <div class="guanyu_right right">
              <h2></h2>
              <h3></h3>
              <p>1600C3</p>
              <p>(Web)www.zsb.cn</p>
              <p>18902221200  / 18923091795 </p>
              <div class="atlas">
                <img src="../../static/images/ditu.png" alt="">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div v-if="this.$store.state.Language=='English'">
      <div class="zsb_relation">
        <img src="../../static/images/zsb_lianxi-en.png" alt="">
      </div>
      <div class="zsb_guanyu_lianxi">
        <div class="w">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/relation' }">About Us</el-breadcrumb-item>
            <el-breadcrumb-item>ContactUs</el-breadcrumb-item>
          </el-breadcrumb>
          <div class="guanyu_lianxi">
            <div class="guanyu_left left">
              <div class="active">{{textEn}}</div>
              <ul>
                <li @click="textCut(1)">ContactUs</li>
                <li @click="textCut(2)">About Us</li>
              </ul>
            </div>
            <div class="guanyu_right right">
              <h2>Contact Information</h2>
              <h3>ZhangShangBang</h3>
              <p>Site1600, huiyuan creative industrial park, no.1600, airport road, baiyun district, guangzhou</p>
              <p>URL(Web)www.zsb.cn</p>
              <p>Phone18902221200  / 18923091795 </p>
              <div class="atlas">
                <img src="../../static/images/ditu.png" alt="">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div v-if="this.$store.state.Language=='Complex'">
      <div class="zsb_relation">
        <img src="../../static/images/zsb_lianxi-ft.png" alt="">
      </div>
      <div class="zsb_guanyu_lianxi">
        <div class="w">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/relation' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
          <div class="guanyu_lianxi">
            <div class="guanyu_left left">
              <div class="active">{{text}}</div>
              <ul>
                <li @click="textCut(1)"></li>
                <li @click="textCut(2)"></li>
              </ul>
            </div>
            <div class="guanyu_right right">
              <h2></h2>
              <h3></h3>
              <p>1600C3</p>
              <p>(Web)www.zsb.cn</p>
              <p>18902221200  / 18923091795 </p>
              <div class="atlas">
                <img src="../../static/images/ditu.png" alt="">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        text: '',
        textEn: 'Contact Us',
      }
    },
    methods: {
      textCut(index) {
        if (index == 1) {
          this.text = '';
          this.textEn = 'Contact Us'
        } else {
          this.text = '';
          this.textEn = 'About Us'
        }
      }
    },
  }

</script>

<style scoped>
  .el-breadcrumb {
    border-bottom: #e1e1e1 solid 1px;
    line-height: 50px;
  }

</style>



